@use "../../global" as g;

.research_intro{
    padding: g.vw(40, 375) 0;

    @include g.mq('md') {
        padding: g.vw(68) 0 g.vw(57);
    }

    @include g.mq('xxl') {
        padding: 68px 0 57px;
    }
    
    .c_low_intro_title  {
        line-height: 1.5em;

        @include g.mq('md') {
            margin-bottom: g.vw(37);
        }
        @include g.mq('xxl') {
            margin-bottom: 37px;
        }
    }
    .c_low_intro_desc{
        @include g.mq('md') {
            padding: 0 g.vw(2);
        }
        @include g.mq('xxl') {
            padding: 0 2px;
        }
    }
}
.problems{
    padding: 0 0 g.vw(60,375);
    @include g.mq('md') {
        padding: g.vw(116) 0 g.vw(62);
    }
    @include g.mq('xxl') {
        padding: 116px 0 62px;
    }
    .block_wrap_title {
        @include g.mq('md') {
            margin-bottom: g.vw(47);
        }
        @include g.mq('xxl') {
            margin-bottom: 47px;
        }
    }
    &_desc{
        letter-spacing: 0;
        margin-bottom: g.vw(30,375);
        @include g.mq('md') {
            margin-bottom: g.vw(44);
        }
        @include g.mq('xxl') {
            margin-bottom: 44px;
        }
    }
    &_list{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &_item{
        width: 100%;
        background: #F5FAFB;
        padding: 35px 30px;
        margin-bottom: g.vw(25,375);
        @include g.mq('md') {
            width: calc(32% - g.vw(11));
            padding: g.vw(50) g.vw(45) g.vw(45);
            margin-bottom: g.vw(40);
        }
        @include g.mq('xxl') {
            width: calc(32% - 11px);
            padding: 50px 45px 45px;
            margin-bottom: 40px;
        }
        .ttl{
            text-align: center;
            font-weight: bold;
            color: #2C68C9;
            font-size: g.vw(22,375);
            letter-spacing: 0;
            line-height: 1.6em;
            margin-bottom: g.vw(10,375);
            @include g.mq('md') {
                font-size: g.vw(25);
                margin-bottom: g.vw(13);
            }
            @include g.mq('xxl') {
                font-size: g.rem(25);
                margin-bottom: 13px;
            }
        }
    }
}
.point{
    padding: g.vw(60,375) 0;
    background: linear-gradient(90deg, rgba(44,104,192,1) 0%,rgba(102,172,192,1) 100%);
    @include g.mq('md') {
        padding: g.vw(119) 0 g.vw(97);
    }
    @include g.mq('xxl') {
        padding: 119px 0 97px;
    }
    .container{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        @include g.mq('md') {
            flex-wrap: nowrap;
        }
    }
    &_img{
        position: relative;
        flex-shrink: 0;
        width: 300px;
        margin: 0 auto g.vw(30,375);
        text-align: center;
        @include g.mq('md') {
            width: g.vw(340);
            left: g.vw(-4);
            margin: 0;
        }
        @include g.mq('xxl') {
            width: 340px;
            left: -4px;
        }
        img{
            width: auto;
            height: auto;
            max-width: 100%;
        }
    }
    &_list{
        width: 100%;
        
        @include g.mq('md') {
            width: g.vw(774);
        }
        @include g.mq('xxl') {
            width: 774px;
        }
    }
    &_item{
        display: flex;
        flex-wrap: wrap;
        margin-bottom: g.vw(30,375);
        @include g.mq('md'){
            align-items: flex-start;
            flex-wrap: nowrap;
            margin-bottom: g.vw(46);
        }
        @include g.mq('xxl') {
            margin-bottom: 46px;
        }
        &:last-child{
            margin-bottom: 0;
        }
    }
    &_heading{
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        flex-shrink: 0;
        margin: 0 auto g.vw(10,375);
        background: #fff;
        border-radius: g.vw(20,375);
        font-family: 'Montserrat', sans-serif;
        font-weight: bold;
        color: #2C68C9;
        padding: g.vw(10,375) g.vw(25,375) g.vw(7,375);
        @include g.mq('md') {
            border-radius: g.vw(20);
            width: g.vw(101);
            margin: 0 g.vw(10) 0 0;
            padding: g.vw(8) g.vw(5) g.vw(7);
        }
        @include g.mq('xxl') {
            width: 101px;
            margin: 0 10px 0 0;
            border-radius: 20px;
            padding: 8px 5px 7px;
        }
        &_txt{
            position: relative;
            display: inline-block;
            line-height: 1;
            font-size: g.vw(14,320);
            margin-right: g.vw(3,375);
            text-transform: capitalize;
            top: g.vw(2,375);
            @include g.mq('md') {
                font-size: g.vw(14);
                margin-right: g.vw(3);
                top: g.vw(4);
            }
            @include g.mq('xxl') {
                font-size: g.rem(14);
                margin-right: 3px;
                top: 4px;
            }
        }
        &_num{
            display: inline-block;
            line-height: 1;
            font-size: g.vw(22,375);
            @include g.mq('md') {
                font-size: g.vw(25);
            }
            @include g.mq('xxl') {
                font-size: g.rem(25);
            }
        }
        &_ttl{
            color: #fff;
            font-size: g.vw(20,375);
            font-weight: bold;
            letter-spacing: 0;
            line-height: 1.5em;
            margin-bottom: g.vw(10,375);
            @include g.mq('md') {
                font-size: g.vw(25);
                margin-bottom: g.vw(15);
            }
            @include g.mq('xxl') {
                font-size: g.rem(25);
                margin-bottom: 15px;
            }
        }
     
    }
    .desc{
        color: #fff;
        @include g.mq('md'){
            letter-spacing: -0.01em;
        }
    }
}
.high_quality{
    padding: g.vw(60,375) 0;
    @include g.mq('md') {
        padding: g.vw(96) 0;
    }
    @include g.mq('xxl') {
        padding: 96px 0;
    }
    .block_wrap_title
    {
        @include g.mq('md') {
            margin-bottom: g.vw(46);
        }
        @include g.mq('xxl') {
            margin-bottom: 46px;
        }
    }
}
.research_flow{
    .block_wrap_title {
        @include g.mq('md'){
            margin-bottom: g.vw(50);
        }
        @include g.mq('xxl') {
            margin-bottom: 50px;
        }
    }
}
.research_qa{
    .block_wrap_title {
        @include g.mq('md'){
            margin-bottom: g.vw(61);
        }
        @include g.mq('xxl') {
            margin-bottom: 61px;
        }
    }
}
